var questions = [
    {
        text: "Выберите верные утверждения:",
        variants: [
            {
                text:"1 === true",
                isCorrect: false
            },
            {
                text:"1 == true",
                isCorrect: true
            },
            {
                text:"null == false",
                isCorrect: false
            },
            {
                text:"null == true",
                isCorrect: false
            },
            {
                text:"true + false == true",
                isCorrect: true
            }
        ]
    },{
        text: "Выберите верный ответ:",
        oneAnswer: true,
        variants: [
            {
                text:"null instanceof Object === true",
                isCorrect: false
            },
            {
                text:"typeof null === typeof new Object()",
                isCorrect: true
            },
            {
                text:"null in Object === true",
                isCorrect: false
            }
        ]
    },
    {
        text: "В каких случаях x === true?:",
        variants: [
            {
                text:"const x = await (async function(){return true})();",
                isCorrect: true
            },
            {
                text:"const x = await (function(){return true})();",
                isCorrect: true
            },
            {
                text:"const x = (async function(){return true})();",
                isCorrect: false
            },
            {
                text:"const x = (async function(){return await true})();",
                isCorrect: false
            },
            {
                text:"const x = (function(){return await true})();",
                isCorrect: true
            }
        ]
    },
    {
        text: "С помощью каких ключевых слов можно объявить переменную в JavaScript (новый синтаксис)?:",
        variants: [
            {
                text:"const",
                isCorrect: true
            },
            {
                text:"int",
                isCorrect: false
            },
            {
                text:"bool",
                isCorrect: false
            },
            {
                text:"let",
                isCorrect: true
            },
            {
                text:"var",
                isCorrect: true
            }
        ]
    }
];

export default questions;


