import React, { Component } from 'react';
import './App.css';

import questions from './files/tests';

const AppStates = {
  hello: 0,
  testing: 1,
  viewingResults: 2
};

function shuffle(a) {
  for (let i = a.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [a[i], a[j]] = [a[j], a[i]];
  }
  return a;
}

class QButton extends Component {
  constructor(props){
    super(props);

    this.selectQuestion = this.selectQuestion.bind(this);
  }
  selectQuestion(){
    this.props.onClick(this.props.index);
  }
  render(){
    var clName = "";
    if(this.props.index === this.props.selectedIndex) clName = "active";
    return (
      <input 
        type="button" 
        className={clName}
        value={this.props.index + 1} 
        onClick={this.selectQuestion}/>
    );
  }
}

class Question extends Component {
  constructor(props){
    super(props);

    this.handleChange = this.handleChange.bind(this);
  }
  handleChange(id){
    this.props.onClick();
    this.setState({
      timeSt: new Date()
    });
  }
  render(){
    var _self = this;
    return (
      <span>
      {this.props.variants.map(function(item, index){
        return (<QuestionItem
          key={index} 
          isCorrect={item.isCorrect}
          index={index}
          onChange={_self.handleChange}
          label={item.text}
          questionIndex={_self.props.index}
          inputType={_self.props.inputType}
        />);
      })}
      </span>
    );
  }
}

class QuestionItem extends Component {
  constructor(props){
    super(props);

    this.handleChange = this.handleChange.bind(this);
  }
  handleChange(){

    let currentValue = allQuestions[this.props.questionIndex].variants[this.props.index].checked;

    if(this.props.inputType === "radio" && currentValue === false){
      for(let i=0, n=allQuestions[this.props.questionIndex].variants.length; i<n; i++){
        allQuestions[this.props.questionIndex].variants[i].checked = false;
      }
      allQuestions[this.props.questionIndex].variants[this.props.index].checked = true;
    }
    else{
      allQuestions[this.props.questionIndex].variants[this.props.index].checked = !currentValue;
    }

    

    this.setState({
      timeSt: new Date()
    });

    this.props.onChange();
    
  }
  render(){
    var clName = "";
    if(this.props.inputType === "radio") clName = "radio";
    return (
      <label className="variant">
        <input 
          className = {clName}
          name={"q"+this.props.questionIndex} 
          type="checkbox"
          value={allQuestions[this.props.questionIndex].variants[this.props.index].checked} 
          checked={allQuestions[this.props.questionIndex].variants[this.props.index].checked} 
          onChange={this.handleChange}/>
        {" "+this.props.label}
      </label>
    );
  }
}

var allQuestions=[];

class Test extends Component {
  constructor(props) {
    super(props);
    let questions = props.questions;

    for(let i=0, n=questions.length, cQuestion = questions[0]; i<n; cQuestion = questions[++i]){
      cQuestion.variants = shuffle(cQuestion.variants);
      for(let j=0, m = cQuestion.variants.length, cVariant = cQuestion.variants[0]; j<m; cVariant = cQuestion.variants[++j]){
        cVariant.checked = false;
      }
    }

    this.questions = shuffle(questions);
    this.currentQuestionId = 0;

    allQuestions = this.questions;

    this.state = {
        currentQuestion: this.questions[this.currentQuestionId],
    };
    this.goToNext = this.goToNext.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.selectQuestion = this.selectQuestion.bind(this);
  }
  goToNext() {
    if(this.currentQuestionId === this.questions.length - 1) this.currentQuestionId = -1;
    this.setState({
      currentQuestion: this.questions[++(this.currentQuestionId)],
    });
  }
  selectQuestion(id) {
    this.setState({
      currentQuestion: this.questions[this.currentQuestionId = id],
    });
  }
  handleChange(){
    this.setState({
      timeSt: new Date()
    });
  }
  render() {
    var _self = this;
    var inputType = "checkbox";
    if(this.state.currentQuestion.oneAnswer) inputType = "radio";
    return (
    <div>
      <div id="q_buttons">
        
        { this.questions.map(function(item, index){
            return (<QButton
              key={index}
              index={index}
              selectedIndex={_self.currentQuestionId}
              onClick={_self.selectQuestion}
              />);
        })}
      </div>
      <h1>
        {this.currentQuestionId + 1}) {this.state.currentQuestion.text}
      </h1>
      
      <div id="customization_result">
        <Question
          variants={this.state.currentQuestion.variants}
          onClick={this.handleChange}
          index={this.currentQuestionId}
          inputType={inputType}
        />
        <input type="button" onClick={this.goToNext} value="Next"/>
      </div>
    </div>
    );
  }
}

class App extends Component {
  constructor(props) {
    
    super(props);
    this.state = {
        status: AppStates.hello,
    };
    this.handleTestStart = this.handleTestStart.bind(this);
    this.handleTestEnd = this.handleTestEnd.bind(this);
  }
  handleTestStart() {
    this.setState({
      status: AppStates.testing
    });
  }
  handleTestEnd() {
    this.setState({
      status: AppStates.viewingResults
    });
  }
  render() {
    let element;
    if (this.state.status === AppStates.hello) {
      element = (
        <input type="button" onClick={this.handleTestStart} value="Start test"/>
      );
    }
    else if (this.state.status === AppStates.testing) {
      element = (
        <div>
          <Test 
            questions={questions}
          />
          <br/>
          <input type="button" onClick={this.handleTestEnd} value="End test"/>
        </div>
      );
    }
    else if (this.state.status === AppStates.viewingResults) {
      var a=0,b=0,c=0,d=0,e=1;
      for(let i=0, n=questions.length, cQuestion = questions[0]; i<n; cQuestion = questions[++i]){
        c=0;
        d=0;
        e=1;
        for(let j=0, m = cQuestion.variants.length, cVariant = cQuestion.variants[0]; j<m; cVariant = cQuestion.variants[++j]){
          if(cVariant.isCorrect === true){
            if(cVariant.checked === cVariant.isCorrect){
              d++;
            } 
            c++;
          }
          else if(cVariant.checked !== cVariant.isCorrect) e*=2;
        }
        a++;
        b += (d/c)/e;
      }
      element = (
        <div>
          <h1>Результат: {Math.floor(100*b/a)}%</h1>
        </div>
      );
    }
    return (
      <div className="App">
        <div id="application">
          <p>
            Тест по JavaScript
          </p>
        </div>
        <div id="content">
          <div id="my_elements">
            {element}
          </div>
        </div>
      </div>
    );
  }
}

export default App;



